import React from 'react';
import {Form,InputGroup,FormControl,Button} from 'react-bootstrap';
export class Recherche extends React.Component{
    constructor(props) {
        super(props);
        this.handleSearch=this.handleSearch.bind(this)
        this.state={ rechercher: ""
        }
    }
    handleSearch(){
        let nom = document.getElementById("inputRechercher").value;
        this.state.onChange({rechercher:nom});
      }
      Effacer(){
        document.getElementById("inputRechercher").value = "";
        this.setState({rechercher:""});
      }
    render() {
        return(
            <Form>
            <InputGroup>
              <FormControl
                id="inputRechercher"
                placeholder="Rechercher"
                onChange={this.handleSearch}
              />
              <InputGroup.Append>
                <Button variant="outline-primary" onClick={this.Effacer}>Annuler</Button>
              </InputGroup.Append>
            </InputGroup>
      
    </Form>
        )
    }
}