import React from "react";
import {Recherche} from './recherche';
import {Albums} from './albums';
import {Row, Button} from 'react-bootstrap';
import {AlbumComplet} from './albumcomplet';
const catalogue = [
    //1
    {
        src : require("../img/trone.jpg"),
        nomAlbum : 'Trône',
        nomArtiste : 'Booba',
    },
    //2
    {
        src : require("../img/derien.jpg"),
        nomAlbum : 'De rien',
        nomArtiste : 'Enima',
        chanson : ['Belzebuth','A la olie']
    },
    //3
    {
        src : require("../img/chaisepliante.jpg"),
        nomAlbum : 'Chaise pliante',
        nomArtiste : 'Hatik',
        chanson : []
    },
    //4
    {
        src : require("../img/lesdernierssalopars.jpg"),
        nomAlbum : 'Les derniers salopards',
        nomArtiste : 'Maes',
        chanson : []
    },
    //5
    {   
        clicker : false,
        src : require("../img/mistral.jpg"),
        nomAlbum : 'Mistral',
        nomArtiste : 'Soso Maness',
        chanson : 'somanees'
    },
    //6
    {
        src : require("../img/zayzay.jpg"),
        nomAlbum : 'ZayZay',
        nomArtiste : 'Fouki',
        chanson : []
    }
]

export class Catalogue extends React.Component{
    constructor(props){
        super(props);
        this.state = {avance:'premier',
                      listeMusique : catalogue,
                      clicker:false,
                      rechercher: ""
                    }
        this.deleteContact=this.deleteContact.bind(this);  
        this.retourArriere= this.retourArriere.bind(this); 
        this.recherche=this.recherche.bind(this); 
    }

    deleteContact =() =>{
        console.log(123)
    //    this.props.clicker
        this.setState({
                    avance:'deuxieme',
                    clicker:true,
                    rechercher:''
                }
                )
       
            }
    afficherCataloge(){
        if (this.state.avance==='premier') {
            return(    
                catalogue.map((element, i) => (
                <Albums clicker={this.state.clicker}
                  key={"Album" + i}
                  src={element.src}
                  nomArtiste={element.nomArtiste}
                  nomAlbum={element.nomAlbum}
                  deleteClickHandler = {this.deleteContact}
                ></Albums>
                )
            )
        )      
    }

        else if (this.state.avance==='deuxieme') {
            return(    
                catalogue.map((element, i) => (
                <Albums clicker={this.state.clicker}
                  key={"Album" + i}
                  src={element.src}
                  nomArtiste={element.nomArtiste}
                  nomAlbum={element.nomAlbum}
                  deleteClickHandler = {this.deleteContact}
                ></Albums>
                )
            )
        )
        
            //return (
            //console.log(this.state.clicker)
        //     <>
        // <h1>Allo</h1>
        // <Albums clicker={this.props.clicker}
        //           src={this.props.src}
        //           nomArtiste={this.props.nomArtiste}
        //           nomAlbum={this.props.nomAlbum}
        //         ></Albums>
                
        //     </>
        //<h1></h1>
        //)    
        } 
       
    }
        
     retourArriere=()=>{
         if(this.state.avance==='deuxieme'){ 
            this.setState({avance:'premier',
                            clicker:false})
         } 
     }
     recherche=()=>{
           let nom = document.getElementById("inputRechercher").value;
        this.setState({rechercher:nom});
        const recherche = this.state.listeMusique.filter(l => l.nomArtiste.toLowerCase().includes(this.state.rechercher.toLowerCase()))
        return recherche.map((element, i) => (
          <Albums
            key={"perso" + i}
            src={element.src}
            nomArtiste={element.nomArtiste}
            nomAlbum={element.nomAlbum}
            chanson={element.chanson}
          ></Albums>
          ));
     }
    
    render() { 
        return( <>
        <Row>
            <Button
            onClick={this.retourArriere}
            >Retour</Button>
            <Recherche
            handleSearch= {this.recherche}
            />
         </Row>
        <Row>
            {this.afficherCataloge()}
        </Row>
        </>)
    }
}